@media only screen and (min-width: 992px) {
  #refund-app {
    max-width: 1150px;
    margin: 0 auto;
  }
  #landing-page {
    justify-content: center;
  }
  .welcome-msg {
    width: 25%;
  }
  .landing-page-div {
    width: 44%;
    margin: 20px 0;
  }
  .landing-page-div h2 {
    font-size: 30px;
  }
  .landing-page-div > div {
    display: flex;
    flex-wrap: no-wrap;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  .landing-page-div > div > div:first-child {
    width: 40%;
    display: flex;
    align-items: center;
  }
  .landing-page-div input,
  .landing-page-div select {
    width: 60%;
    padding: 3px 10px;
  }
  .landing-page-div select {
    height: unset;
  }
  .welcome-msg > div:first-child {
    width: 100%;
    display: flex;
    align-items: flex-start;
    object-fit: contain;
  }
  .welcome-msg .large-logo {
    display: inherit;
  }
  .welcome-msg .small-logo {
    display: none;
  }
  #landing-page > div:nth-child(2) {
    display: flex;
    align-items: flex-start;
    width: 25%;
    padding: 20px 20px 20px 0;
  }
  #landing-page > div:nth-child(2) > div {
    background-color: rgb(169, 135, 247);
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    width: 100%;
    height: 280px;
    padding: 15px;
    font-size: 22px;
    font-family: 'Raleway', sans-serif;
    color: white;
  }
  .landing-page-div > .button-div {
    justify-content: space-between;
    margin: 5px 0;
  }
  .landing-page-div > .login-user {
    justify-content: flex-end;
  }
  .landing-page-div > .new-user {
    border-top: 1px solid rgb(213, 195, 255);
    padding-top: 10px;
    display: flex;
    align-items: center;
    width: 100%;
  }
  .landing-page-div > div > div:first-child {
    font-size: 16px;
  }
  .login .button-div > div:first-child {
    width: 50%;
    text-align: left;
    padding: 0;
    border: none;
    padding: 0;
  }
  .landing-page-div button {
    width: 45%;
    height: 35px;
  }
  .register .button-div {
    justify-content: space-around;
  }
  .modal-landingpg {
    width: 80%;
  }
  .modal-parent-cont {
    width: 44%;
  }
  .modal-container {
    width: 500px;
    display: flex;
    justify-content: center;
  }
  .claim-details-modal-cont {
    position: fixed;
    top: 50%;
    left: 65%;
    transform: translate(-65%, -50%);
  }
  #main-window {
    display: flex;
    flex-wrap: wrap;
  }
  .banner-nav {
    display: flex;
    flex-wrap: wrap;
    width: 120px;
    position: absolute;
    background-color: white;
    background-color: black;
  }
  .banner-holder {
    width: unset;
    position: fixed;
    justify-content: flex-start;
  }
  #banner {
    position: fixed;
    left: unset;
    top: 30px;
    width: 120px;
    border: none;
  }
  #banner .small-logo {
    display: none;
  }
  #banner .large-logo {
    display: flex;
  }
  nav {
    width: 120px;
    border-top: none;
    position: fixed;
    top: 170px;
    background-color: white;
    left: unset;
    border-top: 5px solid rgb(213, 195, 255);
    display: flex;
    justify-content: center;
  }
  nav > div {
    width: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  nav > div > div:first-child {
    width: 100%;
  }
  nav h3 {
    display: inherit;
    margin: 0 0 10px 0;
    padding-bottom: 10px;
    text-align: center;
    font-family: 'Quicksand', sans-serif;
    color: rgb(116, 38, 180);
    font-weight: 700;
    background-color: white;
    border-bottom: 5px solid rgb(213, 195, 255);
  }
  nav .btn-div {
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    background-color: white;
  }
  nav .btn-div > div {
    margin: 15px 0;
  }
  #main-window {
    margin: 10px auto;
    min-height: 90vh;
  }
  #main-window > div:last-child {
    margin: 10px 0 50px 0;
    margin-left: 11%;
    padding-left: 20px;
    width: 100%;
    display: flex;
  }
  #main-window h2 {
    font-size: 30px;
  }
  #claim-details-div > .input-div > div:first-child {
    width: 20%;
    font-size: 18px;
  }
  #claim-details-div > .input-div {
    justify-content: space-between;
  }
  #claim-details-div > .input-div > input {
    width: 80%;
    padding: 3px 10px;
  }
  .claim-section-header h3 {
    padding: 5px 10px;
  }
  .receipt-row,
  .view-claims-row,
  .user-row {
    justify-content: space-between;
    border-bottom: none;
    padding: 0 10px;
  }
  .receipt-row {
    margin-top: none;
  }
  #receipts-section .receipt-row-header,
  .event-row-header,
  .view-claims-row-header,
  #user-mgt-section .user-row-header {
    display: flex;
    flex-wrap: wrap;
    padding: 5px 10px;
    background-color: rgb(254, 255, 170);
  }
  .view-claims-row-header,
  #user-mgt-section .user-row-header {
    border-top: 1px solid rgb(250, 198, 27);
  }
  #receipts-section .receipt-row-header > div,
  #user-mgt-section .user-row-header > div {
    font-weight: 700;
    justify-content: center;
    text-align: center;
  }
  .receipt-rows-section > div:first-child,
  .user-rows-div > div:first-child {
    padding-top: 10px;
  }
  .receipt-rows-section > div:last-child,
  .events-rows-section > div:last-child,
  .claim-rows-div > div:last-child,
  .user-rows-div > div:last-child {
    border-bottom: 1px solid rgb(250, 198, 27);
    padding-bottom: 10px;
  }
  .receipt-row > div,
  .user-row > div {
    width: inherit;
  }
  .receipt-row > div > div,
  .event-row > div > div:first-child,
  .view-claims-row > div > div:first-child,
  .user-row > div > div:first-child {
    display: none;
  }
  #receipts-section .receipt-row input,
  #receipts-section .receipt-row select {
    width: 100%;
    height: 30px;
  }
  .receipt-row-header > div:nth-child(1),
  .receipt-row > div:nth-child(1) {
    width: 15%;
  }
  .receipt-row-header > div:nth-child(2),
  .receipt-row > div:nth-child(2) {
    width: 18%;
  }
  .receipt-row-header > div:nth-child(3),
  .receipt-row > div:nth-child(3) {
    width: 30%;
  }
  .receipt-row-header > div:nth-child(4),
  .receipt-row > div:nth-child(4) {
    width: 20%;
  }
  .receipt-row-header > div:nth-child(5),
  .receipt-row > div:nth-child(5) {
    width: 8%;
  }
  .receipt-row-header > div:nth-child(6),
  .receipt-row > div:nth-child(6) {
    width: 5%;
  }
  #receipts-section .adj-details {
    width: 32%;
  }
  .total-amt > div {
    font-size: 18px;
    padding: 0 5px;
  }
  .claim-details-btn-div {
    position: static;
  }
  .claim-details-btn-div button {
    width: 80px;
  }
  .event-row-header {
    display: flex;
  }
  .event-row-header > div,
  .view-claims-row-header > div {
    justify-content: center;
    font-weight: 700;
    text-align: center;
  }
  .event-row {
    border-bottom: none;
    padding: 5px 15px 0 15px;
  }
  .event-row > div > div:nth-child(2) {
    width: 100%;
  }
  .event-row > div > div:nth-child(2) {
    font-size: 16px;
  }
  .event-row-header > div:nth-child(1),
  .event-row > div:nth-child(1) {
    width: 35%;
  }
  .event-row-header > div:nth-child(2),
  .event-row > div:nth-child(2) {
    width: 35%;
  }
  .event-row-header > div:nth-child(3),
  .event-row > div:nth-child(3) {
    width: 25%;
  }
  #fin-doc-section,
  #change-claim-status {
    width: 49%;
  }
  #fin-doc-section {
    margin-right: 2%;
  }
  #fin-doc-section > div:last-child,
  #change-claim-status > div:last-child {
    padding: 10px 15px;
  }
  #claim-details-div .adj-fin-doc-section {
    width: 100%;
    margin-right: 0;
  }
  .claim-details-btn-div {
    display: flex;
    justify-content: flex-end;
    width: 100%;
  }
  #claim-list-section,
  #user-mgt-section {
    width: 100%;
  }
  .view-claims-row,
  .user-row {
    padding: 5px 15px;
    border-bottom: 1px solid rgb(219, 219, 219);
  }
  .view-claims-row > div > div:nth-child(2),
  .user-row > div > div:nth-child(2) {
    width: 100%;
  }
  .view-claims-row > div:nth-child(1) {
    width: 8%;
  }
  .view-claims-row > div:nth-child(2) {
    width: 25%;
  }
  .view-claims-row > div:nth-child(3) {
    width: 10%;
  }
  .view-claims-row > div:nth-child(4) {
    width: 20%;
  }
  .view-claims-row > div:nth-child(5) {
    width: 10%;
  }
  .view-claims-row > div:nth-child(6) {
    width: 12%;
  }
  .view-claims-row > div:nth-child(7) {
    width: 8%;
  }
  .view-claims-row .btn-div,
  .user-row .btn-div,
  .remove-rcpt-div {
    order: 0;
    margin-bottom: 0;
    justify-content: flex-end;
    align-items: center;
    background-color: white;
    border-top: none;
  }
  .receipt-row > div {
    margin-top: 0;
    margin-bottom: 0;
  }
  .receipt-row-header,
  .event-row-header,
  .view-claims-row-header,
  #user-mgt-section .user-row-header {
    border-bottom: none;
  }
  .user-edit {
    width: 60%;
    padding: 10px;
    border-top: 1px solid rgb(250, 198, 27);
    border-bottom: 1px solid rgb(250, 198, 27);
  }
  .user-edit > div {
    padding: 4px 0;
  }
  .user-row > div:nth-child(1) {
    width: 12%;
  }
  .user-row > div:nth-child(2) {
    width: 10%;
  }
  .user-row > div:nth-child(3) {
    width: 10%;
  }
  .user-row > div:nth-child(4) {
    width: 16%;
  }
  .user-row > div:nth-child(5) {
    width: 16%;
  }
  .user-row > div:nth-child(6) {
    width: 16%;
  }
  .user-row > div:nth-child(7) {
    width: 10%;
  }
  .user-row > div:nth-child(8) {
    width: 8%;
  }
  .view-claims-row > div:nth-child(5) > div:nth-child(2) {
    text-align: center;
  }
  .user-edit > div > div:first-child {
    font-size: 16px;
  }
}
