@import url('https://fonts.googleapis.com/css2?family=Assistant:wght@300;400;500;600;700;800&family=Quicksand:wght@400;500;600;700&family=Raleway:wght@400;500;600;700;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
  font-size: 16px;
  color: rgb(24, 24, 24);
  font-family: 'Assistant', sans-serif;
  box-sizing: border-box;
  font-weight: 500;
}
h2 {
  font-family: 'Quicksand', sans-serif;
  font-size: 24px;
  color: rgb(116, 38, 180);
  letter-spacing: 0;
}
button {
  cursor: pointer;
}
.App {
  background-color: white;
  padding: 10px;
  margin: 0 auto;
  border: none;
}
#refund-app {
  width: 100%;
  margin: 0 auto;
}

/* Modal */
.modal-parent-cont {
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-container {
  width: 100%;
  display: flex;
  justify-content: center;
}
.modal {
  width: 85%;
  display: flex;
  flex-wrap: wrap;
  background-color: white;
  align-content: center;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  filter: drop-shadow(5px 5px 3px rgb(207, 207, 207));
}
.modal > div:first-child {
  width: 100%;
  font-size: 22px;
  font-family: 'Quicksand', sans-serif;
  font-weight: 600;
  text-align: center;
  padding: 5px 0;
  background-color: rgb(116, 38, 180);
  border-top-right-radius: 20px;
  color: white;
}
.modalbody {
  padding: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  border: 1px solid rgb(116, 38, 180);
  border-bottom-left-radius: 20px;
}
.modalbody > div {
  text-align: center;
}
.modal-btn-div {
  width: 100%;
  padding: 20px 20px 0 20px;
  display: flex;
  justify-content: center;
}
.modal-btn-div button {
  padding: 5px 10px;
  border: 2px solid rgb(255, 233, 35);
  background-color: rgb(254, 255, 170);
  border-radius: 5px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
  color: rgb(24, 24, 24);
  width: 30%;
}
.modal-btn-div button:hover {
  background-color: rgb(255, 239, 94);
}
.modal-btn-div button:active {
  background-color: rgb(255, 199, 14);
  color: white;
}
#main-window .modal-container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#main-window .modal-container > .modal {
  width: 94%;
}
#main-window .modal-container .modal-btn-div {
  justify-content: space-around;
}
#main-window .modal-container button {
  width: 40%;
}

/* Banner */
#banner {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 5px 0 0 0;
  border-bottom: 2px solid rgb(116, 38, 180);
  background-color: white;
  position: fixed;
  top: 0%;
  z-index: 1;
}
.banner-holder {
  width: 100%;
  display: flex;
  justify-content: center;
}
.logo-cont {
  width: 150px;
  object-fit: cover;
}
.logo-cont img {
  width: 100%;
}
.logo-cont > img:last-child {
  display: none;
}

/* Navigation */
nav {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 10px 0;
  border-top: 2px solid rgb(116, 38, 180);
  background-color: white;
  z-index: 1;
}
nav h3 {
  display: none;
}
nav .btn-div {
  display: flex;
  justify-content: space-around;
}
nav .btn-div > div {
  display: flex;
  flex-wrap: wrap;
  width: 100px;
  justify-content: center;
}
nav .btn-div button {
  width: 80%;
  height: 50px;
  background-color: white;
  border: none;
  cursor: pointer;
  overflow: hidden;
  object-fit: cover;
}
nav .btn-div img {
  height: 50%;
}
nav .btn-div p {
  width: 100%;
  font-size: 12px;
  color: rgb(116, 38, 180);
  font-family: 'Quicksand', sans-serif;
  font-weight: 500;
  text-align: center;
}

/* Landing Page */

#landing-page {
  min-height: 90vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}
.welcome-msg {
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.welcome-msg div {
  color: rgb(69, 44, 121);
  text-align: center;
  font-size: 13px;
  font-weight: 400;
}
.welcome-msg > div:first-child {
  width: 60%;
  object-fit: cover;
  overflow: hidden;
  margin-top: 10px;
}
.welcome-msg .large-logo {
  display: none;
}
.welcome-msg > div:first-child img {
  width: 100%;
}
#landing-page > div:nth-child(2) {
  display: none;
}

/* landing page: login */

.landing-page-div {
  width: 100%;
  border-top: 2px solid rgb(169, 135, 247);
  border-bottom: 2px solid rgb(169, 135, 247);
  padding: 15px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}
.landing-page-div h2 {
  width: 100%;
  margin-bottom: 5px;
  font-size: 26px;
  font-family: 'Quicksand', sans-serif;
  color: rgb(116, 38, 180);
  text-transform: uppercase;
  font-weight: 700;
}
.landing-page-div h3 {
  width: 100%;
  margin: 10px 0;
}
.demo-roles {
  width: 100%;
  height: 80%;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: space-between;
}
.landing-page-div .demo-roles button {
  margin-top: 10px;
  height: 50px;
}
.landing-page-div div {
  width: 100%;
}
.landing-page-div > div {
  margin-bottom: 5px;
}
.landing-page-div > div > div:first-child {
  font-size: 15px;
  margin: 5px 0;
}
.landing-page-div input,
.landing-page-div select {
  width: 96%;
  outline: 1px solid rgb(201, 181, 247);
  border: none;
  padding: 10px 10px;
}
.landing-page-div select {
  text-transform: capitalize;
  color: rgb(69, 44, 121);
  padding: 10px;
  height: 40px;
}
.landing-page-div button {
  padding: 5px 10px;
  border: 2px solid rgb(255, 233, 35);
  background-color: rgb(254, 255, 170);
  border-radius: 5px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
  color: rgb(24, 24, 24);
  width: 48%;
}
.landing-page-div button:hover {
  background-color: rgb(255, 239, 94);
}
.landing-page-div button:active {
  background-color: rgb(255, 199, 14);
}
.landing-page-div .button-div {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.landing-page-div .error-msg {
  color: rgb(255, 76, 44);
  font-size: 14px;
  padding-top: 10px;
  text-align: center;
}
.register .button-div {
  justify-content: space-between;
}
.login .button-div > div:first-child {
  text-align: center;
  font-size: 14px;
  padding: 10px 0 5px 0;
  border-top: 1px solid rgb(213, 195, 255);
}

/* Main Content */

#main-window > div:last-child {
  margin: 80px 15px 120px 15px;
}
#main-window h2 {
  font-family: 'Quicksand', sans-serif;
  font-size: 24px;
  width: 100%;
  font-weight: 700;
  padding: 10px 0 20px 0;
}

/* Claim Details */

#claim-details-div {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}
#claim-details-div > .input-div {
  width: 100%;
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
}
#claim-details-div > .input-div > div:first-child {
  width: 100%;
  font-weight: 700;
  margin-bottom: 2px;
}
#claim-details-div > .input-div > input {
  width: 100%;
  padding: 2%;
  outline: none;
  border: 1px solid rgb(201, 181, 247);
}

/* Receipts Section */
#receipts-section {
  padding: 10px 0;
  width: 100%;
}
.claim-section-header {
  padding: 5px;
  background-color: rgb(250, 198, 27);
}
.claim-section-header h3 {
  width: 100%;
  text-transform: uppercase;
  font-size: 18px;
  color: white;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
}
.claim-details-btn-div {
  display: flex;
  justify-content: flex-end;
  position: fixed;
  bottom: 80px;
  right: 20px;
}
.claim-details-btn-div > button {
  width: 60px;
  object-fit: contain;
  border-radius: 40%;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.claim-details-btn-div > button img {
  width: 70%;
  background-color: white;
  border-radius: 50%;
}
.claim-details-btn-div > button img {
  border: 4px solid white;
  border-radius: 50%;
}
.claim-details-btn-div > button img:hover {
  border: 4px solid rgb(255, 239, 94);
  border-radius: 50%;
}
.claim-details-btn-div > button p,
.edit-btn p {
  width: 100%;
  font-size: 12px;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  background-color: white;
  padding: 1px;
}
.receipt-row,
.no-receipts,
.no-claims,
.event-row,
.view-claims-row,
.user-row,
#fin-doc-section > div:last-child,
#change-claim-status > div:last-child {
  display: flex;
  flex-wrap: wrap;
  border-left: 1px solid rgb(250, 198, 27);
  border-right: 1px solid rgb(250, 198, 27);
  border-bottom: 1px solid rgb(250, 198, 27);
  padding: 10px;
}
.view-claims-row,
.user-row {
  padding: 0 0 10px 0;
}
.claim-rows-div > div:last-child {
  border-bottom: 1px solid rgb(250, 198, 27);
}
.claim-rows-div > div:first-child,
.user-rows-div > div:first-child {
  border-top: 1px solid rgb(250, 198, 27);
}
.no-receipts,
.no-claims {
  justify-content: center;
  font-size: 13px;
}
.receipt-row-header,
.event-row-header,
.view-claims-row-header,
#user-mgt-section .user-row-header {
  display: none;
}
.receipt-row > div,
.event-row > div,
.view-claims-row > div,
.user-row > div,
.user-edit > div {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2px 0;
}
.view-claims-row > div,
.user-row > div {
  padding: 0 10px;
}
.receipt-row > div > div:first-child,
.event-row > div > div:first-child,
.view-claims-row > div > div:first-child,
.user-row > div > div:first-child,
.user-edit > div > div:first-child {
  font-size: 14px;
  font-weight: 700;
}
.receipt-row > div > input,
.user-edit > div > input,
.user-edit > div > select,
.receipt-row > div > select {
  width: 60%;
  padding: 2px 5px;
  border: 1px solid rgb(160, 160, 160);
}
.user-edit > div > select,
.receipt-row > div > select {
  text-transform: capitalize;
}
.user-edit > div:nth-child(1) > input,
.user-edit > div:nth-child(4) > input {
  text-transform: unset;
}
.receipt-row > div > input:disabled,
.receipt-row > div > select:disabled {
  border: none;
  background-color: white;
  color: rgb(61, 61, 61);
}
.receipt-row > div:nth-child(5) > input {
  text-align: right;
  padding-right: 0;
}
.receipt-row > .remove-rcpt-div,
.user-edit .btn-div {
  justify-content: flex-end;
}
.receipt-row > .remove-rcpt-div > button,
.view-claims-row > .btn-div > button,
#change-claim-status button,
.user-edit button {
  padding: 3px 10px;
  font-size: 15px;
  background-color: rgb(254, 255, 170);
  outline: none;
  border: 1px solid rgb(250, 198, 27);
  border-radius: 5px;
  cursor: pointer;
}
#change-claim-status button {
  width: 24%;
}
.receipt-row > .remove-rcpt-div > button:hover,
.view-claims-row > .btn-div > button:hover,
#change-claim-status button:hover,
.user-edit button:hover {
  background-color: rgb(255, 239, 94);
}
.total-amt {
  margin: 10px 0;
  padding: 5px;
  background-color: rgb(228, 217, 255);
  display: flex;
  justify-content: space-between;
  border-top: 1px solid rgb(169, 135, 247);
  border-bottom: 1px solid rgb(169, 135, 247);
}
.total-amt div {
  font-weight: 700;
}
.user-edit .btn-div > button:last-child {
  margin-left: 5px;
}

/* Events Section */
#events-section,
#fin-doc-section,
#change-claim-status {
  width: 100%;
  margin-bottom: 20px;
}
.event-row > div > div:nth-child(2),
.view-claims-row > div > div:nth-child(2),
.user-row > div > div:nth-child(2) {
  width: 55%;
  font-size: 14px;
}
.event-row > div:first-child > div:nth-child(2) {
  text-transform: capitalize;
}

/* Fin Doc Section */
#fin-doc-section input {
  width: 100%;
  height: 30px;
  text-align: right;
  outline: none;
  border: 1px solid rgb(207, 207, 207);
  padding: 2px 5px;
}
#fin-doc-section input:disabled {
  background-color: white;
  border: none;
}

/* Change Claim Status */
#change-claim-status > div:last-child {
  justify-content: space-between;
}
#change-claim-status select {
  text-transform: capitalize;
  width: 75%;
  height: 30px;
  outline: none;
  border: 1px solid rgb(207, 207, 207);
}

/* Claim List Section */
/* .claim-rows-div > div:first-child,
.user-rows-div > div:first-child {
  border-top: 1px solid rgb(255, 199, 14);
} */
.view-claims-row .btn-div > button:last-child,
.user-row .btn-div > button:last-child {
  margin-left: 5px;
}
.view-claims-row > div:nth-child(3) > div:last-child {
  text-transform: capitalize;
}
.view-claims-row .btn-div,
.user-row .btn-div,
.remove-rcpt-div {
  order: -1;
  justify-content: flex-end;
  margin-bottom: 5px;
  height: 40px;
  align-items: center;
  /* padding: 5px; */
  /* background-color: rgb(254, 255, 170);
  border-top: 1px solid rgb(255, 199, 14); */
}
.view-claims-row .btn-div button,
.user-row .btn-div button,
.receipt-row .remove-rcpt-div button {
  width: 25px;
  height: 25px;
  padding: 0;
  overflow: hidden;
  object-fit: cover;
  border: none;
  background-color: white;
}
.view-claims-row .btn-div img,
.user-row .btn-div img,
.receipt-row .remove-rcpt-div img {
  width: 100%;
}
.view-claims-row .btn-div button:hover,
.user-row .btn-div button:hover {
  background-color: white;
  width: 40px;
  height: 40px;
}
.receipt-row .remove-rcpt-div button:hover {
  background-color: rgb(254, 255, 170);
  border-radius: 50%;
  padding: 1px;
}
.view-claims-row .btn-div button:disabled,
.user-row .btn-div button:disabled {
  width: 25px;
  height: 25px;
  opacity: 30%;
}
