@media only screen and (min-width: 576px) and (max-width: 992px) {
  .welcome-msg {
    width: 40%;
  }
  .landing-page-div {
    width: 50%;
    margin: 20px 0;
  }
  .welcome-msg > div:first-child {
    width: 90%;
    display: flex;
    align-items: flex-start;
  }
  .welcome-msg .large-logo {
    display: inherit;
  }
  .welcome-msg .small-logo {
    display: none;
  }
  .landing-page-div > div > div:first-child {
    font-size: 16px;
  }
  .modal-landingpg {
    width: 100%;
  }
  .modal-container {
    width: 53%;
    display: flex;
    justify-content: center;
  }
  .claim-section-header h3 {
    padding: 5px 10px;
  }
  #main-window h2 {
    font-size: 30px;
  }
  #main-window .modal-container {
    width: 400px;
  }
  #main-window .modal-container > .modal {
    width: 100%;
  }
  #claim-details-div > .input-div {
    justify-content: space-between;
  }
  #claim-details-div > .input-div > div:first-child {
    width: 25%;
    font-size: 18px;
  }
  #claim-details-div > .input-div > input {
    width: 75%;
    padding: 3px 10px;
  }
  .receipt-row,
  .event-row,
  #fin-doc-section > div:last-child,
  #change-claim-status > div:last-child {
    padding: 10px 15px;
  }
  .add-receipt-btn,
  .edit-btn {
    right: 20px;
  }
  .total-amt {
    padding: 10px 15px;
  }
  #fin-doc-section,
  #change-claim-status {
    width: 49%;
  }
  #fin-doc-section {
    margin-right: 2%;
  }
  #claim-details-div .adj-fin-doc-section {
    width: 100%;
    margin-right: 0;
  }
  .user-edit > div {
    padding: 4px 0;
  }
  .user-edit > div > div:first-child {
    font-size: 16px;
  }
}
